
.do{
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.done{
  text-decoration: line-through;
}
.iconDelete{
  font-family: Arial;
  padding: 10px;
  text-transform: lowercase;
  text-decoration: none;
  color: #f00;
}
.check{
  text-transform: lowercase;
  font-family: Arial;
  border-radius: 50%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #eee;
  color: #080;
}



